.stars {
  margin: 16px auto;
  width: 166px;

  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  &__full-stars {
  display: flex;
  }

  &__star {
    background: url(../images/star.svg) no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;
    object-fit: cover;
    margin-left: 4px;


    &:nth-child(-n+4) {
      background-image: url(../images/star-active.svg);
    }

    &:first-of-type {
      margin-left: 0;
    }
  }

  &__reviews {
    color: $primary-text-color;
    text-align: right;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }
}
