.main {
  --column-count: 1;

    &__catalog {
      padding: 50px 0;
      display: grid;
      grid-template-columns: repeat(var(--column-count), 200px);
      grid-gap: 46px 48px;
      justify-content: center;
    }
  }

  @media (min-width: 488px) {
    .main{
    --column-count: 2;
    }
  }

  @media (min-width: 768px) {
    .main{
    --column-count: 3;
    }
  }

  @media (min-width: 1024px) {
    .main{
    --column-count: 4;
    }
  }
