* {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  perspective: 600px;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:not(:hover) {
  transition-duration: .3s;
}

.card__img {
  margin-top: 32px;
  background: url("imac.202c52ff.jpeg") 0 0 / 160px 134px no-repeat;
  width: 160px;
  height: 134px;
  margin-inline: auto;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  width: 166px;
  margin-inline: auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 4px 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  margin-top: 24px;
  justify-content: space-between;
  width: 166px;
  margin-inline: auto;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-decoration: none;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 164px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin: 16px auto;
  display: flex;
}

.stars__full-stars {
  display: flex;
}

.stars__star {
  object-fit: cover;
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:first-of-type {
  margin-left: 0;
}

.stars__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.nav__list {
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 100%;
}

.nav__item:last-of-type a {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link--last-link {
  margin-right: 0;
}

.nav .is-active {
  color: #00acdc;
  display: flex;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  padding: 10px 0;
  display: block;
}

.main {
  --column-count: 1;
}

.main__catalog {
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-gap: 46px 48px;
  justify-content: center;
  padding: 50px 0;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

